.cart-wrapper {
    display: inline-flex;
    justify-content: center;
    width: 100%;
    margin: 15px auto;
    overflow-y: scroll;
}
.cart-container {
    background-color: #fffd95;
    padding: 10px;
    color: #525f4f;
    border-radius: 10px;
    text-align: center;
    display: inline-block;
}
.clear-cart{
    display: inline-block;
    border: 1px solid #ddecda;
    padding: 0px;
    border-radius: 5px;
    background-color: #ddecda;
    cursor: pointer;
}
.title-cart {
    font-size: 18px;
    font-weight: bold;
    color: #525f4f;
    padding: 10px 0;
    text-align: left;
}

.background-cart{
    position: absolute;
    width: 100%;
    height: 100%;
    right: 0;
    top:0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1
}

.cart-wrapper {
    position: fixed;
    height: 100%;
    top: 0;
    right: 0;
    z-index: 1;
    margin-top: 0;
    width: 350px;
    margin-right: 0 !important;
    background-color: #fff;
    border-radius: 10px 0 0 10px;
    background-color: #dcebd9;
}
.cart-container {
    background-color: transparent;
    width: 100%
}

.close-cart {
    display: inline-block;
    cursor: pointer;
}
.mini-cart-items-container {
    margin: 0;
}

.mini-cart-item {
    border-radius: 10px;
    background-color: #fff;
    padding: 10px 5px;
    margin: 5px auto;
    display: inline-flex;
    width: calc(100% - 10px);
}
.mini-cart-item-posotita{
    width: 10%;
}
.mini-cart-item-title{
    width: 70%;
    text-align: left;
}
.mini-cart-item-price{
    width: 20%;
}

.goCheckoutBtn{
    font-weight: bold;
    cursor: pointer;
    color: #fff;
    position: relative;
    padding: 10px;
    background-color: #9cc094;
    border-radius: 10px;
    margin: 10px 0;
}
.header-mini-cart{
    display: inline-flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}
.bi.bi-x-square{
    font-size: 22px;
}