.header-wrapper {
    width: 100%;
    position: fixed;
    padding: 0 0 10px 0;
    text-align: center;  
    z-index: 1;
    display: flex;
    justify-content: center;
}
.header-container{
    max-width: calc( 1000px - 20px );
    background-color: #525f4f/* #8e3c6e skouro red*/;
    margin-top: 0;
    padding: 10px;
    border-radius: 0 0 10px 10px;
    width: 100%;        
}
.header-logo{
    color: #FFFD95;
    font-size: 18px;
    font-weight: 600; 
    letter-spacing: 10px;
    line-height: 34px;
}
.header-description{
    font-size: 14px;
    color: #fff;
    font-weight: 500; 

}
.header-cart i{
    font-size: 20px;
    font-weight: bold;
    color: #fff7e0;
}
.header-title {
    position: relative;
}

.container.header-cart {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: inline-flex;
    justify-content: flex-end;
    padding-right: 43px;
}
.my-cart-text{
    color: #fff7e0;
    font-size: 12px;
    margin-right: 10px;
}
.my-cart-on-header{
    display: inline-flex;
    align-items: center;
    cursor: pointer;
}

@media (min-width: 1300px){
    .header-container{
        max-width: calc(100% - 100px)
    }
}

@media (max-width: 800px){
    .my-cart-text{
       display: none;
    }
}

.badge {
    padding-left: 9px;
    padding-right: 9px;
    -webkit-border-radius: 9px;
    -moz-border-radius: 9px;
    border-radius: 9px;
      font-size: 12px;
      background: #fffd95;
      color: #8e3c6e;
      padding: 0 5px;
      vertical-align: top;
      margin-left: -10px; 
      margin-top: -20px;
      font-weight: bold;
  }