.product {
	background-color: #fff;
    margin: 5px auto;
    display: flex;
    border-radius: 5px;
    padding: 5px;
    flex-wrap: wrap;
    align-items: center;
    box-shadow: 0 0 10px -3px #8c8c8c;
}

.firstcol {
}

.title {
	font-weight: 600;
	color: #8E0505
}
.thirdcol{
    display: flex;
    justify-content: flex-end;
}
.fullPrice {
	display: flex;
	justify-content: flex-end;
	align-items: center;
}

.description {
	width: 100%;
	font-weight: 500;
	font-size: 14px;
	color: #7a7070;
}
.quantity{
	color: #495f5b;
	margin: 0 4px;
}
.price {
	font-weight: bold;
	color: #525f4f;
}

.initPrice {
    color: #7a7070;
    text-decoration: line-through;
    padding: 0 10px;
    font-weight: 600;
    font-size: 12px;
}
.thirdcol i.bi{
	color: #8e3c6e;
	font-size: 20px;
	cursor: pointer;
}
.product {
    width: calc(100% - 20px);
    margin-left: 5px;
}

.firstcol {
	width: 60%;
}
.secondcol{
	width: 15%;	
}
.thirdcol{
	width: 25%;
}
@media (min-width: 800px){	
    .product{
		width: calc( 50% - 20px);
    	margin-left: 5px;
    }	
	.firstcol {
		width: 65%;
	}
	.secondcol{
		width: 20%;	
	}
	.thirdcol{
		width: 15%;
	}
}